import Vue from 'vue';
import cookie from 'vux/src/tools/cookie/index.js'


const KEYS = ['roleId', 'userId', 'ptoken'];

const user = {
state: {},
getters: {
user: state => state
},
actions: {
userinfo: ({ commit }, data) => {
if (cookie.get('userId')) {
return Vue.http
.get('puser/queryById', {
params: Object.assign(data || {}, {
userId: cookie.get('userId')
})
})
.then(result => {
commit('setUser', result.entry);
return result;
});
} else {
return Promise.reject();
}
},
login: ({ commit }, data) => Vue.http.post('puser/tologin', data),
logout: ({ commit }) =>
new Promise((resolve, reject) => {
commit('clearUser');
resolve();
})
},
mutations: {
setUser(state, data) {
Object.keys(data).map(key => {
state[key] = data[key];
});







},
clearUser(state) {
Object.keys(state).map(key => delete state[key]);
KEYS.forEach(key =>
cookie.remove(key, {

path: '/'
})
);
}
}
};

export default user;



